/** @jsx jsx */
import { jsx } from '@emotion/core';
import {
  formatDistanceToNow,
  differenceInMinutes,
  differenceInDays,
  format,
  parseISO,
} from 'date-fns';
import { FC } from 'react';

import { Devlog } from './devlog-template';

type DevlogTemplateMetaProps = Pick<Devlog, 'date' | 'updated'>;

const formatDate = (date: Date) => {
  return differenceInDays(new Date(), date) < 31
    ? formatDistanceToNow(date, { addSuffix: true })
    : format(date, 'PPP');
};

const DevlogTemplateMeta: FC<DevlogTemplateMetaProps> = ({ date, updated }) => {
  const parsedDate = parseISO(date);
  const parsedUpdated = parseISO(updated);
  const isUpdatedRecently = differenceInMinutes(parsedUpdated, parsedDate) <= 30;

  return (
    <p>
      <small>
        Posted {formatDate(parsedDate)}{' '}
        {isUpdatedRecently ? null : `(updated ${formatDate(parsedUpdated)})`}
      </small>
    </p>
  );
};

export default DevlogTemplateMeta;
