/** @jsx jsx */
import { css, jsx } from '@emotion/core';
import { Link, graphql } from 'gatsby';
import Img, { FluidObject, FixedObject } from 'gatsby-image';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import { FC } from 'react';

import DevlogTemplateMeta from './devlog-template-meta';
import Layout from './layout';
import Seo from './seo';
import { rhythm } from './styles/typography';

type HeroImage = {
  sharp: {
    fluid: FluidObject;
    fixed: FixedObject;
  };
};

export type Devlog = {
  body: string;
  title: string;
  author: string;
  image: HeroImage;
  slug: string;
  date: string;
  updated: string;
};

type DevlogTemplateProps = { data: { devlog: Devlog } };

export const query = graphql`
  query($pageID: String!) {
    devlog(id: { eq: $pageID }) {
      body
      title
      slug
      author
      date
      updated
      image {
        sharp: childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
          fluid(maxWidth: 700, maxHeight: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  }
`;

const DevlogTemplate: FC<DevlogTemplateProps> = ({ data }) => {
  const { title, image, slug, body, date, updated } = data.devlog;

  return (
    <Layout>
      <Seo title={title} image={image.sharp.fixed.src} urlSlug={slug} />

      <article>
        <h1>{title}</h1>

        <DevlogTemplateMeta date={date} updated={updated} />

        <Img
          css={css`
            margin-bottom: ${rhythm(1)};
            box-shadow: 0 0 6px rgba(0, 0, 0, 0.11), 0 0 0 1px rgba(0, 0, 0, 0.05);
            border-radius: 3px;
          `}
          fluid={image.sharp.fluid}
          alt={title}
        />

        <MDXRenderer>{body}</MDXRenderer>
      </article>

      <Link
        css={css`
          display: inline-block;
        `}
        to="/"
      >
        &larr; back to home
      </Link>
    </Layout>
  );
};

export default DevlogTemplate;
